"use client"

import Link from "next/link"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Input } from "@/components/ui/input"
import { Badge } from "@/components/ui/badge"
import { Tabs, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { useEffect, useState, useCallback, useRef } from "react"
import {
  TrendingUp, MessageSquare, LogOut, Trash2, Loader2, 
  ShieldCheck, Send, Users, User, Bot, Circle, Sparkles, CreditCard
} from "lucide-react"
import Image from "next/image"

// 브라우저용 라이브러리 (net 모듈 에러 방지)
import { Client } from '@stomp/stompjs'
import SockJS from 'sockjs-client'

interface LinkedAccount {
  bankAccount: string; bankName: string; createdAt: string; locked: boolean;
}

interface ChatRoom {
  id: string;
  chatRoomId?: string;
  title?: string;
  name?: string;
  roomType: 'GROUP' | 'DIRECT' | 'AI';
  lastMessage?: string;
}

interface ChatMessage {
  senderId: string;
  senderName: string;
  content: string;
  createdAt: string;
}

export default function DashboardPage() {
  const router = useRouter()
  const [userName, setUserName] = useState("")
  const [userId, setUserId] = useState("")
  const [isLoading, setIsLoading] = useState(true)
  const [linkedAccounts, setLinkedAccounts] = useState<LinkedAccount[]>([])

  // 채팅 관련 상태
  const [activeTab, setActiveTab] = useState("GROUP")
  const [rooms, setRooms] = useState<ChatRoom[]>([])
  const [currentRoom, setCurrentRoom] = useState<ChatRoom | null>(null)
  const [messages, setMessages] = useState<ChatMessage[]>([])
  const [inputValue, setInputValue] = useState("")
  const [isConnected, setIsConnected] = useState(false)
  
  const stompClient = useRef<Client | null>(null)
  const scrollRef = useRef<HTMLDivElement>(null)
  const subscriptions = useRef<Record<string, any>>({})

  const API_BASE_URL = 'http://sparta-nextme.xyz:18080'

  const fetchData = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    const storedUserId = localStorage.getItem("userId")
    if (!token) { router.push("/login"); return; }
    setUserId(storedUserId || "")
    setUserName(localStorage.getItem("name") || "사용자")
    try {
      const response = await fetch("http://34.50.7.8:3000/v1/account/user-account", {
        method: "POST",
        headers: { "Authorization": `Bearer ${token}`, "Content-Type": "application/json" },
        body: JSON.stringify({ userId: storedUserId }) 
      })
      if (response.ok) {
        const data = await response.json()
        if (data.isSuccess) setLinkedAccounts(data.result || [])
      }
    } catch (e) { console.error(e) } finally { setIsLoading(false) }
  }, [router])

  const loadRooms = async (type: string) => {
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch(`${API_BASE_URL}/v1/chats/room?roomType=${type}`, {
        headers: { Authorization: `Bearer ${token}` }
      })
      const data = await res.json()
      setRooms(data.rooms || data || [])
    } catch (e) { console.error(e) }
  }

  const connectChat = useCallback(() => {
    const token = localStorage.getItem("accessToken")
    if (!token) return
    const socket = new SockJS(`${API_BASE_URL}/ws/chat`)
    const client = new Client({
      webSocketFactory: () => socket,
      connectHeaders: { Authorization: `Bearer ${token}` },
      onConnect: () => {
        setIsConnected(true)
        loadRooms(activeTab)
      },
      onDisconnect: () => setIsConnected(false),
    })
    client.activate()
    stompClient.current = client
  }, [activeTab])

  useEffect(() => {
    fetchData()
    connectChat()
    return () => { stompClient.current?.deactivate() }
  }, [fetchData, connectChat])

  const selectRoom = (room: ChatRoom) => {
    const roomId = room.id || room.chatRoomId
    if (!roomId) return
    setCurrentRoom(room)
    setMessages([])
    if (subscriptions.current[roomId]) return
    stompClient.current?.subscribe(`/topic/chat.room.${roomId}`, (msg) => {
      const received = JSON.parse(msg.body)
      setMessages((prev) => [...prev, received])
    })
    subscriptions.current[roomId] = true
  }

  const sendMessage = () => {
    const roomId = currentRoom?.id || currentRoom?.chatRoomId
    if (!inputValue.trim() || !stompClient.current?.connected || !roomId) return
    stompClient.current.publish({
      destination: `/app/chat.send/${roomId}`,
      body: JSON.stringify({ content: inputValue, roomType: currentRoom.roomType })
    })
    setInputValue("")
  }

  useEffect(() => {
    if (scrollRef.current) scrollRef.current.scrollTop = scrollRef.current.scrollHeight
  }, [messages])

  if (isLoading) return <div className="h-screen flex items-center justify-center bg-white"><Loader2 className="animate-spin text-primary w-10 h-10" /></div>

  return (
    <div className="min-h-screen bg-[#F8FAFC] text-slate-900">
      {/* --- HEADER --- */}
      <header className="h-16 border-b bg-white/80 backdrop-blur-md flex items-center justify-between px-8 sticky top-0 z-50">
        <Link href="/dashboard" className="transition-opacity hover:opacity-80">
          <Image src="/logo.png" alt="Logo" width={110} height={30} className="h-7 w-auto" />
        </Link>
        <div className="flex items-center gap-4">
          <div className="flex items-center gap-2 px-3 py-1.5 bg-slate-100 rounded-full border border-slate-200">
            <Circle className={`h-2 w-2 fill-current ${isConnected ? 'text-green-500' : 'text-red-400'}`} />
            <span className="text-[10px] font-bold text-slate-500 tracking-tight">{isConnected ? 'LIVE CONNECT' : 'DISCONNECTED'}</span>
          </div>
          <Button variant="ghost" size="icon" onClick={() => { localStorage.clear(); router.push("/"); }} className="text-slate-400 hover:text-rose-500">
            <LogOut className="h-5 w-5" />
          </Button>
          <Avatar className="h-9 w-9 border-2 border-white shadow-sm ring-1 ring-slate-200">
            <AvatarFallback className="bg-primary text-white text-xs font-bold">{userName.charAt(0)}</AvatarFallback>
          </Avatar>
        </div>
      </header>

      <main className="container mx-auto px-4 py-8 grid lg:grid-cols-12 gap-8">
        {/* --- LEFT SIDE: Financial Report --- */}
        <div className="lg:col-span-7 space-y-8">
          <section>
            <h1 className="text-3xl font-black tracking-tight text-slate-900 mb-2">안녕하세요, {userName}님</h1>
            <p className="text-slate-500 font-medium">실시간 데이터 기반 재무 분석 대시보드입니다.</p>
          </section>

          {/* Asset Info Cards */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <Card className="p-6 border-none shadow-sm bg-white hover:shadow-md transition-shadow">
              <div className="flex justify-between items-start mb-4">
                <p className="text-[11px] font-black text-slate-400 uppercase tracking-wider">총 자산</p>
                <div className="p-1.5 bg-blue-50 rounded-lg"><TrendingUp className="h-4 w-4 text-blue-500" /></div>
              </div>
              <p className="text-2xl font-black text-slate-800 tracking-tighter">₩45,280,000</p>
            </Card>
            <Card className="p-6 border-none shadow-sm bg-white hover:shadow-md transition-shadow">
              <div className="flex justify-between items-start mb-4">
                <p className="text-[11px] font-black text-slate-400 uppercase tracking-wider">월 저축</p>
                <div className="p-1.5 bg-primary/10 rounded-lg"><Sparkles className="h-4 w-4 text-primary" /></div>
              </div>
              <p className="text-2xl font-black text-primary tracking-tighter">₩1,200,000</p>
            </Card>
            <Card className="p-6 border-none shadow-sm bg-white hover:shadow-md transition-shadow">
              <div className="flex justify-between items-start mb-4">
                <p className="text-[11px] font-black text-slate-400 uppercase tracking-wider">월 지출</p>
                <div className="p-1.5 bg-rose-50 rounded-lg"><CreditCard className="h-4 w-4 text-rose-500" /></div>
              </div>
              <p className="text-2xl font-black text-rose-500 tracking-tighter">₩2,450,000</p>
            </Card>
          </div>

          {/* Account List Section */}
          <Card className="p-8 border-none shadow-sm bg-white overflow-hidden">
            <div className="flex items-center justify-between mb-8">
              <h2 className="text-lg font-black flex items-center gap-2"><ShieldCheck className="h-5 w-5 text-primary" /> 계좌 연동 현황</h2>
              <Button variant="outline" size="sm" className="rounded-full text-xs font-bold border-slate-200">전체보기</Button>
            </div>
            <div className="grid gap-4">
              {linkedAccounts.length > 0 ? linkedAccounts.map((acc, i) => (
                <div key={i} className="group flex items-center justify-between p-5 rounded-2xl bg-slate-50 hover:bg-white border border-transparent hover:border-slate-100 transition-all hover:shadow-sm">
                  <div className="flex items-center gap-4">
                    <div className="w-12 h-12 rounded-2xl bg-white border border-slate-100 flex items-center justify-center font-black text-xs text-primary shadow-sm group-hover:scale-110 transition-transform">{acc.bankName.slice(0,2)}</div>
                    <div>
                      <p className="text-sm font-black text-slate-800 mb-0.5">{acc.bankName}</p>
                      <p className="text-[11px] text-slate-400 font-medium tracking-tight">{acc.bankAccount}</p>
                    </div>
                  </div>
                  <Badge variant="secondary" className="bg-emerald-50 text-emerald-600 border-none font-bold text-[10px] py-1 px-3 rounded-full">Active</Badge>
                </div>
              )) : (
                <div className="text-center py-20 bg-slate-50 rounded-3xl border-2 border-dashed border-slate-200">
                  <p className="text-sm text-slate-400 font-bold">연동된 계좌가 없습니다.</p>
                </div>
              )}
            </div>
          </Card>
        </div>

        {/* --- RIGHT SIDE: Unified Chat Center --- */}
        <div className="lg:col-span-5 h-[calc(100vh-140px)] sticky top-24">
          <Card className="h-full flex flex-col border-none shadow-2xl bg-white overflow-hidden rounded-[2.5rem] border border-white/50">
            <Tabs defaultValue="GROUP" className="flex flex-col h-full" onValueChange={(v) => { setActiveTab(v); loadRooms(v); }}>
              <div className="p-6 bg-slate-50/50 border-b">
                <TabsList className="grid grid-cols-3 w-full bg-white border p-1 rounded-2xl h-12 shadow-inner">
                  <TabsTrigger value="GROUP" className="text-[11px] font-black gap-2 transition-all"><Users className="h-4 w-4" /> 그룹</TabsTrigger>
                  <TabsTrigger value="DIRECT" className="text-[11px] font-black gap-2 transition-all"><User className="h-4 w-4" /> 전문가</TabsTrigger>
                  <TabsTrigger value="AI" className="text-[11px] font-black gap-2 transition-all"><Bot className="h-4 w-4" /> AI</TabsTrigger>
                </TabsList>
              </div>

              <div className="flex-1 flex overflow-hidden">
                {/* Rooms List (Side) */}
                <div className="w-1/3 border-r bg-slate-50/20 overflow-y-auto scrollbar-hide">
                  {rooms.length > 0 ? rooms.map(room => (
                    <div 
                      key={room.id || room.chatRoomId} 
                      onClick={() => selectRoom(room)}
                      className={`px-5 py-6 cursor-pointer border-b border-slate-100 transition-all ${currentRoom?.id === (room.id || room.chatRoomId) ? 'bg-white font-black border-l-4 border-l-primary shadow-[inset_0_0_20px_rgba(0,0,0,0.02)]' : 'hover:bg-white/50'}`}
                    >
                      <p className="text-[12px] truncate text-slate-700 leading-tight mb-1">{room.title || room.name}</p>
                      <Badge variant="outline" className="text-[8px] font-bold py-0 h-4 border-slate-200 uppercase tracking-tighter opacity-60">Room</Badge>
                    </div>
                  )) : (
                    <div className="py-20 text-center opacity-20"><MessageSquare className="mx-auto h-6 w-6" /></div>
                  )}
                </div>

                {/* Message Display (Chat Area) */}
                <div className="flex-1 flex flex-col bg-white">
                  {!currentRoom ? (
                    <div className="flex-1 flex flex-col items-center justify-center text-slate-300 p-8 text-center animate-in fade-in zoom-in duration-500">
                      <div className="w-20 h-20 bg-slate-50 rounded-full flex items-center justify-center mb-6 shadow-inner">
                        <MessageSquare className="opacity-20 h-8 w-8 text-primary" />
                      </div>
                      <p className="text-[11px] font-black uppercase tracking-widest text-slate-400">대화방을 선택하세요</p>
                    </div>
                  ) : (
                    <>
                      <div className="px-6 py-4 border-b bg-white flex justify-between items-center shadow-sm z-10">
                        <p className="text-xs font-black truncate text-slate-800">{currentRoom.title || currentRoom.name}</p>
                        <div className="flex items-center gap-1.5">
                           <div className="h-2 w-2 rounded-full bg-green-500 animate-pulse" />
                           <span className="text-[9px] font-bold text-slate-400 uppercase tracking-tighter">Active</span>
                        </div>
                      </div>

                      <div ref={scrollRef} className="flex-1 overflow-y-auto p-6 space-y-6 scrollbar-hide bg-slate-50/10">
                        {messages.map((msg, i) => (
                          <div key={i} className={`flex flex-col ${msg.senderId === userId ? 'items-end' : 'items-start animate-in slide-in-from-left-2 duration-300'}`}>
                            {msg.senderId !== userId && <span className="text-[10px] font-black text-slate-400 mb-2 ml-1">{msg.senderName}</span>}
                            <div className={`px-4 py-3 rounded-2xl text-[13px] leading-relaxed max-w-[85%] font-medium shadow-sm border ${
                              msg.senderId === userId 
                              ? 'bg-primary text-white border-transparent rounded-tr-none shadow-primary/20' 
                              : 'bg-white text-slate-700 border-slate-100 rounded-tl-none'
                            }`}>
                              {msg.content}
                            </div>
                          </div>
                        ))}
                      </div>

                      <div className="p-6 border-t bg-white">
                        <div className="relative flex items-center gap-2 bg-slate-100 p-1.5 rounded-2xl border border-slate-200 focus-within:ring-2 ring-primary/20 transition-all shadow-inner">
                          <Input 
                            value={inputValue} 
                            onChange={(e) => setInputValue(e.target.value)}
                            onKeyPress={(e) => e.key === 'Enter' && sendMessage()}
                            className="flex-1 h-11 rounded-xl bg-transparent border-none shadow-none focus-visible:ring-0 text-sm font-medium"
                            placeholder="메시지를 입력하세요..."
                          />
                          <Button size="icon" onClick={sendMessage} className="h-11 w-11 rounded-xl shadow-lg bg-primary hover:bg-primary/90 transition-all hover:scale-105 active:scale-95">
                            <Send className="h-4 w-4" />
                          </Button>
                        </div>
                      </div>
                    </>
                  )}
                </div>
              </div>
            </Tabs>
          </Card>
        </div>
      </main>
    </div>
  )
}